/* src/pages/InstructionsPage.css */
.instructions-page {
    color: white;
    padding: 20px;
    background-color: #333;
  }
  
  .instructions-page h1 {
    font-size: 2em;
    margin-bottom: 20px;
  }
  
  .instructions-page ul {
    margin-top: 10px;
  }
  
  .instructions-page ul li {
    margin-bottom: 10px;
  }
  
  .instructions-page button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    background-color: #00bcd4;
    color: white;
    border: none;
    border-radius: 5px;
  }
  