.home-page {
    text-align: center;
    margin-top: 50px;
  }
  
  .home-page h1 {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  
  button {
    padding: 10px 20px;
    margin: 10px;
    font-size: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #61dafb;
    color: #282c34;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #21a1f1;
  }