/* src/assets/styles/styles.css */

/* General Styling */
body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background-color: #282c34;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  button {
    padding: 10px 20px;
    margin: 10px;
    font-size: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #61dafb;
    color: #282c34;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #21a1f1;
  }
  
  /* Home Page Styling */
  .home-page {
    text-align: center;
  }
  
  .home-page h1 {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  
  /* Game Page Styling */
  .game-page {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Game Grid Styling */
  .game-grid {
    position: relative;
    width: 400px;
    height: 400px;
    background-color: #1e1e1e;
    border: 2px solid #61dafb;
    margin-top: 20px;
  }
  
  /* Snake Styling */
  .snake-segment {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: #4caf50;
    border-radius: 3px;
  }
  
  /* Fruit Styling */
  .fruit {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: #e91e63;
    border-radius: 50%;
  }
  
  /* Scoreboard Styling */
  .scoreboard {
    display: flex;
    justify-content: space-between;
    width: 300px;
    margin-top: 10px;
    font-size: 1.2rem;
  }
  
  /* Game Over Page Styling */
  .game-over-page {
    text-align: center;
  }
  
  .game-over-page h1 {
    font-size: 3rem;
    margin-bottom: 10px;
  }
  
  .game-over-page h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  /* High Scores Page Styling */
  .high-scores-page {
    text-align: center;
  }
  
  .high-scores-page h1 {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  